@import url("https://fonts.googleapis.com/css2?family=Poppins:ital,wght@1,800&display=swap");

@font-face {
  font-family: "Open Sans";
  src: url("../src/assets/fonts/Poppins-Black.otf") format("otf"),
    url("../src/assets/fonts/Poppins-BlackItalic.otf") format("otf"),
    url("../src/assets/fonts/Poppins-Bold.otf") format("otf"),
    url("../src/assets/fonts/Poppins-BoldItalic.otf") format("otf"),
    url("../src/assets/fonts/Poppins-ExtraBold.otf") format("otf"),
    url("../src/assets/fonts/Poppins-ExtraBoldItalic.otf") format("otf"),
    url("../src/assets/fonts/Poppins-ExtraLight.otf") format("otf"),
    url("../src/assets/fonts/Poppins-ExtraLightItalic.otf") format("otf"),
    url("../src/assets/fonts/Poppins-Medium.otf") format("otf"),
    url("../src/assets/fonts/Poppins-SemiBold.otf") format("otf"),
    url("../src/assets/fonts/Poppins-Regular.otf") format("otf");
}

@tailwind base;
@tailwind components;
@tailwind utilities;

*,
::after,
::before {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.neonText {
  color: #f6ff00;
  text-shadow: 0 0 7px #fff, 0 0 92px #f6ff00, 0 0 102px #f6ff00,
    0 0 151px #f6ff00;
}
